import { Tag } from "antd";

export const columns = [
  {
    title: "",
    dataIndex: "date",
    key: "date",
    render: (text) => <p>{text}</p>,
  },
  {
    title: "",
    dataIndex: "time",
    key: "time",
  },
  {
    title: "",
    key: "tags",
    dataIndex: "tags",
    render: (_, { tags }) => (
      <>
        {tags.map((tag) => {
          let color = tag.length > 5 ? "red" : "green";
          if (tag === "SCHEDULED") {
            color = "blue";
          }

          return (
            <Tag color={color} key={tag}>
              {statusMatches[tag].toUpperCase()}
            </Tag>
          );
        })}
      </>
    ),
  },
  {
    title: "",
    dataIndex: "teams",
    key: "teams",
  },
  {
    title: "",
    dataIndex: "score",
    key: "score",
  },
];

export const statusMatches = {
  SCHEDULED: "",
  LIVE: "  ",
  IN_PLAY: " ",
  PAUSED: "",
  FINISHED: "",
  POSTPONED: "",
  SUSPENDED: "",
  CANCELED: "",
};
